import Bmob from "hydrogen-js-sdk";
import common from '@/utils/common.js';
import print from '@/utils/print.js';

let uid;
let that;

export default {
	data() {
		return {
			stock: '', //仓库
			products: null,
			button_disabled: false,
			beizhu_text: "",
			real_money: 0, //实际付款金额
			all_money: 0, //总价
			producer: null, //制造商
